<template>
  <div class="home-container">
    <header>
      <el-card>
        <div class="header flex">
          <div class="flex-start">
            <el-image class="avatar-style" :src="instructorInfo.img"></el-image>

            <div class="user-info-style">
              <p class="user-name-style">{{ instructorInfo.name }}</p>
              <ul class="tag-list-style flex-start">
                <li class="tag-item-style">
                  <p class="tag-value-style">
                    {{ instructorInfo.lecture_count }}
                  </p>
                  <p class="tag-label-style">讲座场次</p>
                </li>
                <li class="tag-item-style">
                  <p class="tag-value-style">
                    {{ instructorInfo.sum_visitor }}
                  </p>
                  <p class="tag-label-style">学员人数</p>
                </li>
              </ul>
            </div>
          </div>

          <el-button
            class="switch-account-style"
            @click="$router.push('/login')"
          >
            <i class="el-icon-sort"></i>
            切换账号
          </el-button>
        </div>
      </el-card>
    </header>

    <article>
      <el-card>
        <el-upload
          class="upload-demo"
          drag
          action="#"
          :show-file-list="false"
          :before-upload="beforeUpload"
          :http-request="httpRequest"
        >
          <div>
            <div v-show="fileList.length > 0">
              <el-table
                :data="fileList"
                style="width: 100%"
                :max-height="maxHeight"
              >
                <el-table-column prop="name" label="文件名称">
                </el-table-column>
                <el-table-column label="状态" width="180" align="center">
                  <el-image
                    :src="require('@/assets/icon/success_icon.png')"
                  ></el-image>
                </el-table-column>
              </el-table>
            </div>
            <div v-show="!fileList.length > 0">
              <div class="el-upload__text">
                <div class="tips-box">
                  <p class="tips-title">拖拽文件到这里</p>
                  <p class="tips-describe">
                    目前仅支持Excel文件，更多格式正在开发中，敬请期待
                  </p>
                  <el-button class="tips-btn" round type="primary"
                    >选择文件</el-button
                  >
                </div>
              </div>
            </div>
          </div>

          <div class="el-upload__tip" slot="tip">
            目前仅支持Excel文件，点击上方空白处上传文件
          </div>
        </el-upload>
      </el-card>
    </article>
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      instructorInfo: {
        name: "",
        img: "",
        lecture_count: 0,
        sum_visitor: 0,
      },
      // 文件对象
      file: null,
      // 文件上传列表
      fileList: [],
      maxHeight: this.$cpx(352),
    };
  },
  created() {
    this.$store.dispatch("instructor/instructorInfoGetAPI");
    this.instructorInfo = this.$store.getters.instructorInfo;
    console.log("instructorInfo :>> ", this.instructorInfo);
  },
  methods: {
    /**
     * 上传文件之前的钩子，参数为上传的文件，若返回 false 或者返回 Promise 且被 reject，则停止上传。
     */
    beforeUpload(file) {
      const isJPG =
        file.type ===
          "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" ||
        file.type === "application/vnd.ms-excel";

      if (!isJPG) {
        this.$message.error("只能上传 Excel 文件!");
      }

      this.file = file;

      return isJPG;
    },
    /**
     * 覆盖默认的上传行为，可以自定义上传的实现
     */
    httpRequest() {
      this.uploadExcelAPI(this.file);
    },

    /**
     * 添加用户试卷
     */
    async uploadExcelAPI(file) {
      const data = {
        file: file,
      };
      const res = await this.$request("excel.uploadExcel", data);
      console.log("res :>> ", res);
      this.lectureInstructorUploadAPI(res);
    },

    /**
     * 讲师批量导入-JSON上传
     */
    async lectureInstructorUploadAPI(importData) {
      const data = {
        json: JSON.stringify(importData),
      };
      console.log("data :>> ", data);
      const res = await this.$request("excel.lectureInstructorUpload", data);
      console.log("res :>> ", res);
      this.fileList.push(this.file);
      console.log("this.fileList :>> ", this.fileList);
      this.$message.success("导入成功!");
    },
  },
};
</script>

<style lang="scss" scoped>
.home-container {
  padding: px(24);
  header {
    .avatar-style {
      width: px(90);
      height: px(121);
      border-radius: px(16);
      border: px(2) solid #0077ff;
      overflow: hidden;
    }

    .header {
      width: 100%;
      .user-info-style {
        margin-left: px(24);
        .user-name-style {
          font-size: px(24);
          font-weight: bold;
          color: #333333;
        }

        .tag-list-style {
          margin-top: px(35);
          .tag-item-style {
            margin-right: px(152);
            .tag-value-style {
              font-size: px(24);
              font-weight: bold;
              color: #333333;
            }
            .tag-label-style {
              font-size: px(14);
              color: #cccccc;
            }
          }
        }
      }
      .switch-account-style {
        background: #f7f7f7;
      }
    }
  }

  article {
    margin-top: px(12);
    .upload-demo {
      ::v-deep {
        .el-upload {
          width: 100%;
          .el-upload-dragger {
            width: 100%;
            height: px(352);
            background: #eee;
            // border-style: solid;
            .el-upload__text {
              margin-top: 8%;
              .tips-box {
                .tips-title {
                  font-size: px(20);
                  font-weight: bold;
                  color: #333333;
                }
                .tips-describe {
                  margin-top: px(12);
                  font-size: px(14);
                  color: #cccccc;
                }
                .tips-btn {
                  margin-top: px(45);
                  width: px(159);
                  height: px(56);
                  background: #0077ff;
                  border-radius: px(28);
                  font-size: px(16);
                  font-weight: bold;
                  color: #ffffff;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>
